html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0;
  font-family: Roboto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  flex: 1 0 auto;

  background: linear-gradient(
    138deg,
    var(--primary-background) 20%,
    var(--secondary-background) 50%,
    var(--primary-background) 80%
  );
  /* & p:last-child {
    margin-bottom: 2rem;
    margin-top: 2rem;
  } */
}

.recharts-responsive-container {
  margin: 0rem auto 0rem auto;
  transition: all 0.1s linear;
  overflow: visible;
}

/*

  tooltip

*/

.recharts-default-tooltip {
  width: 210px;
  background: var(--primary-background);
  text-align: left;
  padding: 0.9rem 1rem;
  box-shadow: 0.1em 0.1em 5em 0em rgba(0, 0, 0, 0.5);
  border: 1px solid #ccc;
  border-radius: 0.4rem;
  /* transform: perspective(300px) rotateY(-2deg); */
  & .recharts-tooltip-label {
    padding: 0;
    margin: 0 auto 0.4rem 0;
    text-align: center;
    font-weight: bold;
  }
  & .recharts-tooltip-item-list {
    margin: 0 auto;
    padding: 0 0.5rem;
    & .recharts-tooltip-item {
      list-style: none;
      margin-bottom: 0.3rem;
      margin-right: 0.5rem;
      font-size: 0.8rem;
      &.recharts-tooltip-item-highlight {
        background-color: var(--secondary-background);
      }
      & .recharts-tooltip-item-circle {
        border-radius: 0.5rem;
        width: 2rem;
        height: 0.5rem;
        display: inline-block;
      }
      & .recharts-tooltip-item-name {
        width: 50px;
        padding: 0 0.25rem;
        color: var(--primary-text);
      }
      & .recharts-tooltip-item-value {
        float: right;
        color: var(--primary-text);
        text-align: right;
        border-radius: 5px;
        padding: 0.2rem 0.5rem;
        font-weight: bold;
      }
    }
  }
}

.recharts-surface {
  /* margin-left: -10%; */
  /* transform: perspective(300px) rotateY(-2deg); */
}

/*

  legend and brush

*/

.recharts-legend-wrapper {
  /* transform: perspective(0px) rotateY(0deg); */
}
.recharts-legend-item-text,
.recharts-tooltip-item-name,
.recharts-surface path {
  user-select: none;
  font-family: Oswald;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

.recharts-default-legend li {
  cursor: pointer;
}
.recharts-brush {
  & > rect:first-child {
    fill: var(--primary-background);
    fill-opacity: 1;
  }
  & .recharts-brush-slide {
    fill: var(--secondary-background);
    fill-opacity: 1;
  }
}

.desactivated {
  display: none;
}

/* recharts lines */
:not(.minimap) {
  .recharts-line:not(.desactivated) {
    & path {
      transition: stroke-width 0.2s linear, stroke 0.2s linear;
      /* stroke-width: 2; */
    }
    &:has(+ .recharts-active-dot:hover) {
      & path {
        stroke-width: 10;
      }
    }
    &:hover {
      & path {
        stroke-width: 10;
      }
    }
  }
}

.recharts-reference-line text {
  font-size: 0.7rem;
  fill: var(--primary-background);
  text-transform: none;
  font-style: italic;
  display: none;
  & tspan {
    fill: var(--primary-text);
  }
}

/**/
.scale-switch {
  fill: var(--secondary-text);
  text-transform: capitalize;
  text-decoration: underline;
  cursor: pointer;
  transform: all 1s linear;
  &:hover {
    fill: var(--primary-text);
  }
}

/*

dashboard

*/

.dashboard {
  display: flex;
  flex-wrap: wrap;

  & .graph {
    min-width: 60%;
    flex-grow: 1;
  }
  & .carto {
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    margin-bottom: 2rem;
    flex-grow: 1;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    & .info {
      width: 300px;
      color: var(--primary-text);
      background: var(--secondary-background);
      border-radius: 0.5rem;
      padding: 1rem;
      & .date {
        display: inline-block;
        font-family: Oswald;
        color: var(--secondary-text);
        font-weight: bold;
        font-size: 1.5rem;
        margin: 0 0 1.5rem 0;
        vertical-align: sub;
      }
    }
    & .carto-legend {
      color: var(--primary-text);
      font-weight: bold;
      font-size: 0.8rem;
      display: flex;
      margin-top: 1rem;
      & .carto-legend-title {
        margin-right: 1rem;
      }
      & .carto-legend-element {
        & .carto-legend-scale {
          vertical-align: top;
          cursor: mark;
          display: inline-block;
          vertical-align: center;
          width: 2.5rem;
          height: 2.5rem;
          line-height: 2.5rem;
        }
      }
    }
  }
}

/*

minimap

*/

.minimap {
  width: 100%;
  margin-top: 1rem;
  margin-bottom: 2.5rem;
  background: var(--secondary-background);
  & .minimap-background {
  }
  & .brush-bar {
    position: absolute;
    margin-top: -80px;
    margin-left: -50%;
  }
  & .handle {
    fill: var(--primary-color);
  }
  & .brush-slider {
    /* transition: all 0.2s; */
  }
}

/*

carto svg

*/

.france-svg {
  min-height: 320px;
  width: 300px;

  & g {
    /* stroke: #ccc; */
  }

  & text.france-svg-label {
    text-transform: capitalize;
    text-anchor: end;
    font-size: 1rem;
    fill: var(--primary-color);
    font-weight: bold;
    stroke: var(--secondary-background);
    stroke-width: 1;

    cursor: pointer;
    & tspan.label-city {
      font-family: Oswald;
      fill: hsl(331, 95%, 56%);
      font-weight: bold;
    }
  }

  & .station {
    cursor: pointer;
    transition: fill 0.3s, stroke 0.3s;
    &.selected {
      transition: fill 0.3s, stroke 0.3s;
    }
    &.activated {
      transition: fill 0.3s, stroke 0.3s;
    }
  }

  & .station-neon {
    cursor: pointer;
    fill: none;
    stroke: hsla(60, 100%, 60%, 0);
    stroke-width: 3px;
    transition: fill 0.3s, stroke 0.3s;
    filter: ;
    &.selected {
      stroke: hsla(60, 100%, 60%, 1);
      filter: url(#neon);
      transition: fill 0.3s, stroke 0.3s;
    }
    &.activated {
      stroke: hsla(60, 100%, 70%, 1);
      filter: url(#neon);
      transition: fill 0.3s, stroke 0.3s;
    }
  }
}

/*

aniamtion istemphover

move from selected to activated cycle

*/

.idf-svg {
  min-height: 250px;
}

.counter {
  width: 300px;
  color: #eeeeee;
  font-family: Oswald;
  display: flex;
  align-items: center;
  min-height: 3.2rem;
  margin-bottom: 0.5rem;
  & .city,
  & .concentration {
    font-size: 1.4rem;
    text-transform: capitalize;
    opacity: 0%;
    transition: all 1s;
  }

  & .city {
    flex-basis: 50%;
    text-align: right;
    opacity: 100%;
    var(--primary-text)-space: nowrap;
  }
  & .concentration {
    padding: 0.5rem 0.75rem;
    border-radius: 0.5rem;
    display: inline-block;
    margin-left: 2rem;
    opacity: 100%;
  }
}

/* element styling */
p {
  &.info {
    max-width: 720px;
    /* margin: 0.5rem; */
    border-radius: 0.5rem;
    padding: 0.75rem 1rem;
    background: var(--green);
  }
  &.warn {
    max-width: 720px;
    /* margin: 0.5rem; */
    border-radius: 0.5rem;
    padding: 1rem 1rem;
    background: var(--yellow);
  }
}

text {
  font-family: Roboto;
}
a {
  color: var(--primary-text);
  font-family: Oswald;
}

h1,
h2,
h3 {
  color: #eeeeee;
  font-family: Oswald;
  padding: 1rem 1rem;
  margin: 0 auto;
}
p {
  line-height: 1.4rem;
  margin: 1rem auto;
  max-width: 720px;
  color: var(--primary-text);
  padding: 0rem 1rem;
  text-align: left;
}

/*


*/

@keyframes wavecolor {
  0% {
    background-image: var(--wave);
  }
  50% {
    background-image: var(--wave);
  }
  55% {
    background-image: var(--wave-color);
  }
  70% {
    background-image: var(--wave);
  }
  100% {
    background-image: var(--wave);
  }
}

footer,
h1 {
  text-shadow: 0px 0px 5px var(--primary-background),
    0px 0px 8px var(--primary-background),
    0px 0px 10px var(--primary-background);
  background: var(--primary-background);
  background-image: var(--wave);
  border-top: 1px solid var(--secondary-background);

  animation: 30s infinite wavecolor;
}
h1 {
  & span {
    color: var(--primary-color);
  }
}
footer {
  & div {
    padding: 2rem;
    max-width: 480px;
    margin: auto;
    margin-bottom: 1rem;
    color: gray;
  }
}
.preset {
  color: var(--primary-text);
  margin-bottom: 2rem;
  & .reset {
    background: none;
    font-size: 0.9rem;
    color: var(--primary-text);
    font-family: Roboto;
    display: inline-block;
    font-style: normal;
    cursor: pointer;
    margin-left: 0.25rem;
    padding: 0.25rem 0.5rem;
    border-radius: 0.5rem;
    border: 3px solid rgb(102, 102, 102);
    transition: all 0.2s linear;
    &:hover {
      border: 3px solid var(--primary-text);
    }
  }
  & .selected {
    border: 3px solid var(--primary-color);
  }
}

/* mediaqueries */

@media screen and (min-width: 0px) {
  abbr[data-title] {
    position: relative;

    /* ensure consistent styling across browsers */
    text-decoration: underline dotted;
  }

  abbr[data-title]:hover::after,
  abbr[data-title]:focus::after {
    content: attr(data-title);

    /* position tooltip like the native one */
    position: absolute;
    left: 0;
    bottom: -30px;
    width: auto;
    var(--primary-text)-space: nowrap;

    /* style tooltip */
    background-color: #1e1e1e;
    color: #fff;
    border-radius: 3px;
    box-shadow: 1px 1px 5px 0 rgba(0, 0, 0, 0.4);
    font-size: 14px;
    padding: 3px 5px;
  }
}

/* CSS styles for screens with a width greater than 600px */
@media screen and (min-width: 600px) {
  .recharts-reference-line text {
    font-size: 0.7rem;
    display: block;
  }
}

/* CSS styles for screens with a width greater than 800px */
@media screen and (min-width: 800px) {
  .recharts-reference-line text {
    font-size: 0.9rem;
    display: block;
  }
  .dashboard {
    & .graph {
    }
    & .carto {
      flex-grow: 0;
    }
    & header {
    }
  }
}
